<template>
  <div class="van-datetime-dict">
    <van-field
      v-on="$listeners"
      type="text"
      v-bind="$attrs"
      readonly
      clickable
      :value="showValue"
      is-link
      @click="handleClick"
    >
      <template
        v-for="(slot, key) in $scopedSlots"
        :slot="key"
        slot-scope="scope"
      >
        <slot :name="key" v-bind="scope"></slot>
      </template>
      <template v-for="(slot, key) in $slots" :slot="key">
        <slot :name="key"></slot>
      </template>
    </van-field>
    <van-popup v-model="showPicker" round position="bottom">
      <van-datetime-picker
        ref="datetimePicker"
        v-model="currentDate"
        v-bind="$attrs"
        @change="handleChange"
        @cancel="handleCancel"
        @confirm="onConfirm"
      >
        <template v-slot:confirm>
          <div class="slot-btn">
            <span v-if="showForver" class="select" @click.stop="forever">{{
              showValueLabel
            }}</span
            ><span>{{ $attrs.confirmButtonText || "" }}</span>
          </div>
        </template>
      </van-datetime-picker>
    </van-popup>
  </div>
</template>
<script>
import { FormatDate, IsDate } from "~/functions/date-format"
export default {
  name: "van-datetime-dict",
  data() {
    return {
      showValue: "",
      showPicker: false,
      svalue: this.value,
      currentDate: null,
      indexs: null
    }
  },
  props: {
    showValueLabel: {
      type: String,
      default: ""
    },
    forverValue: {
      default: "forver"
    },
    //
    showForver: {
      type: Boolean,
      default: false
    },
    //
    dateFormat: {
      type: String
    },
    //
    defaultVal: {
      type: String,
      default: ""
    },
    value: {} //v-model
  },
  computed: {},
  watch: {
    //
    value(val) {
      this.svalue = val //propsresultdatamyResult
    },
    svalue(val, oldVal) {
      if (val !== oldVal) {
        this.$emit("input", val) //myResult
      }
    },
    showPicker(val) {
      if (this.indexs) {
        this.$nextTick(() => {
          this.$refs.datetimePicker.getPicker().setIndexes(this.indexs)
        })
        return
      }
      if (val) {
        this.$nextTick(() => {
          setTimeout(() => {
            this.indexs = this.$refs.datetimePicker.getPicker().getIndexes()
          }, 100)
        })
      }
    }
  },
  created() {
    if (this.showForver && this.value === this.forverValue) {
      this.showValue = this.showValueLabel
    } else {
      // 
      if (this.value) {
        if (!IsDate(this.value)) {
          this.showValue = this.defaultVal
          return
        }
        if (this.$attrs.type === "time") {
          this.currentDate = this.value
        } else if (this.$attrs.type === "month-day") {
          const date = (new Date().getFullYear() + "-" + this.value).replace(
            /-|\./g,
            "/"
          )
          this.currentDate = new Date(date)
        } else if (this.$attrs.type === "datehour") {
          const date = (this.value + ":00:00").replace(/-|\./g, "/")
          this.currentDate = new Date(date)
        } else {
          const date = this.value.replace(/-|\./g, "/")
          this.currentDate = new Date(date)
        }

        this.formatValue(this.currentDate)
      }
    }
  },
  methods: {
    getRef() {
      return this.$refs.datetimePicker
    },
    handleChange(value) {
      this.$emit("change", value)
    },
    //
    handleCancel() {
      this.showPicker = false
      this.$emit("cancel")
    },
    //
    handleClick() {
      if (this.$attrs.disabled === true || this.$attrs.disabled === "") {
        return
      }
      this.showPicker = true
    },
    formatValue(value) {
      const dateObj = {
        "date": "date",
        "year-month": "month",
        "month-day": "MM-dd",
        "datehour": "yyyy-MM-dd hh"
      }
      if (this.$attrs.type === "time") {
        this.svalue = value
      } else {
        const dateFormat =
          this.dateFormat || dateObj[this.$attrs.type] || "datetime"
        this.svalue = FormatDate(value, dateFormat, this.defaultVal)
      }
      this.showValue = this.svalue
    },
    //
    onConfirm(value) {
      this.formatValue(value)
      this.indexs = this.$refs.datetimePicker.getPicker().getIndexes()
      this.showPicker = false
      this.$emit("dictChange", value)
      this.$emit("confirm", value)
    },
    forever() {
      this.svalue = this.forverValue
      this.showValue = this.showValueLabel
      this.showPicker = false
      this.$emit("forver", this.forverValue)
    }
  }
}
</script>
<style scoped>
.van-datetime-dict ::v-deep .slot-btn .select {
  margin-right: 14px;
}
</style>
