<template>
  <div class="van-field-dict">
    <van-field ref="VanFieldDict" v-on="$listeners" v-bind="$attrs" @focus="handleFocus" @blur="handleBlur"></van-field>
    <van-field v-if="desensitization" v-show="showCamouflage"  :name="$attrs.name&&$attrs.name + 'Camouflage'"  v-bind="$attrs"  class="camouflage" v-model="sValue"></van-field>
  </div>
</template>

<script>
import {Desensitization} from "~/functions/mask"
export default {
  name: "van-field-dict",
  props: {
    defaultVal: {
      type: String, //
      default: ""
    },
    // 
    maskType: {
      type: String,
      validate(value) {
        return ["mobile", "address", "idCard", "name"].includes(value)
      }
    },
    maskStart: {
      type: Number
    },
    maskEnd: {
      type: Number
    },
    maskMiddle: {
      type: Number
    },
    maskSymbol: {
      type: String,
      default: "*"
    }
  },
  data() {
    return {
      sValue: "",
      showCamouflage: false
    }
  },
  computed: {
    desensitization() {
      return this.maskType || this.maskStart
    },
    maskOptions() {
      const obj = {}
      if (this.defaultVal) {
        obj.defaultVal = this.defaultVal
      }
      if (this.maskType) {
        obj.maskType = this.maskType
      }
      if (this.maskStart) {
        obj.start = this.maskStart
      }
      if (this.maskEnd) {
        obj.end = this.maskEnd
      }
      if (this.maskMiddle) {
        obj.middle = this.maskMiddle
      }
      if (this.maskSymbol) {
        obj.symbol = this.maskSymbol
      }
      return obj
    }
  },
  created() {
    this.handleBlur()
  },
  methods: {
    getRef() {
      return this.$refs.VanFieldDict
    },
    //
    handleFocus() {
      if (this.$attrs.readonly === true || this.$attrs.readonly === ""){
        return 
      }
      this.showCamouflage = false
    },
    //
    handleBlur() {
      if (this.desensitization) {
        this.sValue = Desensitization(this.$attrs.value, this.maskOptions)
        this.showCamouflage = true
      }
    }
  }
}
</script>

<style lang="less" scoped>
.van-field-dict{
  position: relative;
  .van-cell.van-field.camouflage{
    position: absolute;
    left: 0;
    top: 0;
    pointer-events: none;
  }
}
</style>