<template>
  <van-checkbox-group ref="VanCheckboxGroup" :value="svalue" v-on="$listeners" v-bind="$attrs" @change="handdleChange">
    <van-checkbox
      v-for="(item, index) in list"
      :shape="shape"
      :label-disabled="labelDisabled"
      :label-position="labelPosition"
      :icon-size="iconSize"
      :checked-color="checkedColor"
      :bind-group="bindGroup"
      :disabled="item.disabled"
      :name="item[dictConfig.format.value]"
      :key="index"
      @change="handleCheckboxChange"
      @click="handleCheckboxClick"
    >
      {{item[dictConfig.format.label]}}
    </van-checkbox>
  </van-checkbox-group>
</template>
<script>
import store from "~/store/index.js"
export default {
  name: "van-checkbox-dict",
  data() {
    return {
      svalue: this.value || [],
      result: [],
      list: [], //
      type: "" //
    }
  },
  props: {
    value: {}, //v-model
    shape: {
      type: String,
      default: "round"
    },  
    labelDisabled: {
      type: Boolean,
      default: false
    }, 
    labelPosition: {
      type: String,
      default: "right"
    },
    iconSize: {
      type: [Number, String],
      default: "20px"
    },
    checkedColor: {
      type: String,
      default: "#1989fa"
    },  
    bindGroup: {
      type: Boolean,
      default: true
    },  
    //
    keyValue: {
      type: Boolean,
      default: false
    },
    dictType: {
      type: [String, Object]
    }, //
    //
    disableObj: {
      type: Object
    },
    //
    filterDataFun: {
      type: Function
    },
    //
    disabledDataFun: {
      type: Function
    }
  },
  computed: {
    dictCodes() {
      return store.state.dictCodes
    },
    dictConfig() {
      return store.state.dictConfig
    },
    isGettingAllDictTypes() {
      return store.state.isGettingAllDictTypes
    },
    isGettingDictTypes() {
      return store.state.isGettingDictTypes
    }
  },
  watch: {
    isGettingDictTypes(val) {
      if (!val.includes(this.type)) {
        this.getDictionary_option()
      }
    },
    // 
    isGettingAllDictTypes(val) {
      if (!val) {
        this.getDictionary_option()
      }
    },
    //
    value(val) {
      this.svalue = val //propsresultdatamyResult
    },
    svalue(val, oldVal) {
      if (val !== oldVal) {
        this.$emit("input", val) //myResult
      }
    }
  },
  mounted() {
    this.getDictionary_option()
  },
  methods: {
    // 
    handleCheckboxClick(val) {
      this.$emit("checkboxClick", val)
    },  
    // 
    handleCheckboxChange(val) {
      this.$emit("checkboxChange", val)
    },  
    // 
    async getDictionary_option() {
      if (!this.dictType) {
        return
      }
      //
      let dictData = []
      if (typeof this.dictType === "object") {
        this.type = this.dictType.type
        await store.actions.getDictDataObj(this.type)
        dictData = [].concat(this.dictCodes[this.type])
        const {
          filters,
          filterType = this.dictConfig.format.value,
          reverse = false
        } = this.dictType
        const filtersArr =
          typeof filters === "string" ? filters.split(",") : filters
        if (reverse) {
          dictData = dictData.filter(item => {
            return !filtersArr.includes(item[filterType])
          })
        } else {
          dictData = dictData.filter(item => {
            return filtersArr.includes(item[filterType])
          })
        }
      } else {
        this.type = this.dictType
        await store.actions.getDictDataObj(this.type)
        dictData = [].concat(this.dictCodes[this.type])
      }

      //
      if (this.filterDataFun) {
        dictData = this.filterDataFun(dictData)
      } 

      //
      if (this.disabledDataFun) {
        dictData = this.disabledDataFun(dictData)
      }

      if (this.disableObj) {
        const {
          disableValue,
          disableType = this.dictConfig.format.value,
          reverse = false
        } = this.disableObj
        const disableList =
          typeof disableValue === "string"
            ? disableValue.split(",")
            : disableValue
        if (reverse) {
          dictData = dictData.map(item => {
            const targetItem = JSON.parse(JSON.stringify(item))
            targetItem.disabled =
              !disableList.includes(targetItem[disableType]) ||
              targetItem.disabled
            return targetItem
          })
        } else {
          dictData = dictData.map(item => {
            const targetItem = JSON.parse(JSON.stringify(item))
            targetItem.disabled =
              disableList.includes(targetItem[disableType]) ||
              targetItem.disabled
            return targetItem
          })
        }
      }
      this.list = dictData
    },
    // 
    handdleChange(val) {
      /* emit
         */
      if (!this.keyValue) {
        this.$emit("dictChange", this.svalue)
        return
      }

      const itemArr = this.list.filter(item => {
        return this.svalue.includes(item[this.dictConfig.format.value])
      })
      this.$emit("dictChange", itemArr)
    },

    getRef() {
      return this.$refs.VanCheckboxGroup
    }
  }
}
</script>

