<template>
  <van-dropdown-item ref="VanDropdownItemDict" v-on="$listeners" v-bind="$attrs" @change="handdleChange" :options="options">
    <template v-for="(slot, key) in $scopedSlots" :slot="key" slot-scope="scope">
      <slot :name="key" v-bind="scope"></slot>
    </template>
    <template v-for="(slot, key) in $slots" :slot="key">
      <slot :name="key"></slot>
    </template>
  </van-dropdown-item>
</template>
<script>
import store from "~/store/index.js"
export default {
  name: "van-dropdown-item-dict",
  data() {
    return {
      list: [], //
      type: "" //
    }
  },
  props: {
    //
    keyValue: {
      type: Boolean,
      default: false
    },
    dictType: {
      type: [String, Object]
    }, //
    disableObj: {
      type: Object
    },
    //
    filterDataFun: {
      type: Function
    },
    //
    disabledDataFun: {
      type: Function
    }
  },
  computed: {
    dictCodes() {
      return store.state.dictCodes
    },
    dictConfig() {
      return store.state.dictConfig
    },
    isGettingAllDictTypes() {
      return store.state.isGettingAllDictTypes
    },
    isGettingDictTypes() {
      return store.state.isGettingDictTypes
    },
    options() {
      return this.list.map(item => {
        return {
          text: item[this.dictConfig.format.label],
          value: item[this.dictConfig.format.value],
          icon: item.icon || ""
        }
      })
    }
  },
  watch: {
    isGettingDictTypes(val) {
      if (!val.includes(this.type)) {
        this.getDictionary_option()
      }
    },
    // 
    isGettingAllDictTypes(val) {
      if (!val) {
        this.getDictionary_option()
      }
    }
  },
  mounted() {
    this.getDictionary_option()
  },
  methods: {
    // 
    async getDictionary_option() {
      if (!this.dictType) {
        return
      }
      //
      let dictData = []
      if (typeof this.dictType === "object") {
        this.type = this.dictType.type
        await store.actions.getDictDataObj(this.type)
        dictData = [].concat(this.dictCodes[this.type])
        const {
          filters,
          filterType = this.dictConfig.format.value,
          reverse = false
        } = this.dictType
        const filtersArr =
          typeof filters === "string" ? filters.split(",") : filters
        if (reverse) {
          dictData = dictData.filter((item) => {
            return !filtersArr.includes(item[filterType])
          })
        } else {
          dictData = dictData.filter((item) => {
            return filtersArr.includes(item[filterType])
          })
        }
      } else {
        this.type = this.dictType
        await store.actions.getDictDataObj(this.type)
        dictData = [].concat(this.dictCodes[this.type])
      }

      //
      if (this.filterDataFun) {
        dictData = this.filterDataFun(dictData)
      }

      //
      if (this.disabledDataFun) {
        dictData = this.disabledDataFun(dictData)
      }

      if (this.disableObj) {
        const {
          disableValue,
          disableType = this.dictConfig.format.value,
          reverse = false
        } = this.disableObj
        const disableList =
          typeof disableValue === "string"
            ? disableValue.split(",")
            : disableValue
        if (reverse) {
          dictData = dictData.map((item) => {
            const targetItem = JSON.parse(JSON.stringify(item))
            targetItem.disabled =
              !disableList.includes(targetItem[disableType]) ||
              targetItem.disabled
            return targetItem
          })
        } else {
          dictData = dictData.map((item) => {
            const targetItem = JSON.parse(JSON.stringify(item))
            targetItem.disabled =
              disableList.includes(targetItem[disableType]) ||
              targetItem.disabled
            return targetItem
          })
        }
      }

      this.list = dictData
    },
    // 
    handdleChange(val) {
      /* emit
         */
      this.$emit("dictChange", val)
    },
    getRef() {
      return this.$refs.VanDropdownItemDict
    }
  }
}
</script>
