import store from "~/store"
/**
 * @description: codelabel
 * @param {*}
 * @return {*}
 * @author: syx
 */
import { GetTreeItemByCode } from "~/functions/list-tree"
import {
  GetTreeLabelByCodesDothings,
  GetTreeCodeByLabelsDothings
} from "~/functions/tree-dict"

export const GetTreeLabelByCodeFilter = (
  val,
  type,
  options
) => {
  const defaultOptions = {
    defaultVal: "",
    formatFun: (arr, treeSetting) => {
      return arr
        .map((item) => {
          return item[treeSetting.labelField]
        })
        .join(",")
    },
    treeSetting: {}
  }
  Object.assign(defaultOptions, options)
  const { treeSetting } = store.state.dictConfig
  const resultSetting = Object.assign(treeSetting, defaultOptions.treeSetting)
  const dictList = store.state.dictCodes

  let list = []
  //
  if (!dictList || !dictList[type]) {
    store.actions.getDictDataObj(type).then((data) => {
      list = data[type]
      if (!list) {
        return new Error(`${type} `)
      }
      const targetItem = list.find((item) => {
        return item[resultSetting.idField] === val
      })
      return targetItem
        ? defaultOptions.formatFun(
          GetTreeItemByCode(
            targetItem[resultSetting.idField],
            list,
            resultSetting
          ),
          resultSetting
        )
        : defaultOptions.defaultVal
    })
  } else {
    list = dictList[type]
    const targetItem = list.find((item) => {
      return item[resultSetting.idField] === val
    })
    return targetItem
      ? defaultOptions.formatFun(
        GetTreeItemByCode(
          targetItem[resultSetting.idField],
          list,
          resultSetting
        ),
        resultSetting
      )
      : defaultOptions.defaultVal
  }
}

export const GetTreeLabelByCodesFilter = (
  vals,
  type,
  options
) => {
  const defaultOptions = {
    defaultVal: "",
    formatFunIn: (arr, treeSetting) => {
      return arr
        .map((item) => {
          return item[treeSetting.labelField]
        })
        .join("/")
    },
    treeSetting: {},
    formatFunOut: (arr) => {
      return arr.join(",")
    },
    spacer: ","
  }
  Object.assign(defaultOptions, options)
  let values = []
  if (Array.isArray(vals)) {
    values = vals
  } else if (typeof vals === "string") {
    values = vals.split(defaultOptions.spacer)
  }

  const { treeSetting } = store.state.dictConfig
  const resultSetting = Object.assign(treeSetting, defaultOptions.treeSetting)
  const dictList = store.state.dictCodes

  let list = []
  //
  if (!dictList || !dictList[type]) {
    store.actions.getDictDataObj(type).then((data) => {
      list = data[type]
      if (!list) {
        return new Error(`${type} `)
      }
      const targetArr = GetTreeLabelByCodesDothings(
        values,
        list,
        defaultOptions.defaultVal,
        defaultOptions.formatFunIn,
        resultSetting
      )
      return targetArr && targetArr.length > 0
        ? defaultOptions.formatFunOut(targetArr)
        : defaultOptions.defaultVal
    })
  } else {
    list = dictList[type]
    const targetArr = GetTreeLabelByCodesDothings(
      values,
      list,
      defaultOptions.defaultVal,
      defaultOptions.formatFunIn,
      resultSetting
    )
    return targetArr && targetArr.length > 0
      ? defaultOptions.formatFunOut(targetArr)
      : defaultOptions.defaultVal
  }
}

