import store from "~/store"

/**
 * @description: codelabel
 * @param {*}
 * @return {*}
 * @author: syx
 */
import {GetTreeItemByCode} from "~/functions/list-tree"

export const GetTreeLabelByCode = (
  val,
  type,
  options
) => { 
  const defaultOptions = {
    defaultVal: "",
    formatFun: (arr, treeSetting) => {
      return arr.map((item) => {
        return item[treeSetting.labelField]
      }).join(",")
    },
    treeSetting: {}
  }
  Object.assign(defaultOptions, options)
  return new Promise((resolve, reject) => {
    const {treeSetting} = store.state.dictConfig
    const resultSetting = Object.assign(treeSetting, defaultOptions.treeSetting)
    const dictList = store.state.dictCodes
     
    let list = []
    //
    if (!dictList || !dictList[type]) {
      if (store.state.isGettingDictTypes.includes(type) || store.state.isGettingAllDictTypes) { //  
        const timer = setInterval(() => {
          if (!(store.state.isGettingDictTypes.includes(type) || store.state.isGettingAllDictTypes)) {
            clearInterval(timer)
            list = store.state.dictCodes[type]
            if (!list) {
              reject(`${type} `)
              return
            }
            const targetItem = list.find(item => { return item[resultSetting.idField] === val })
            resolve(targetItem ? defaultOptions.formatFun(GetTreeItemByCode(targetItem[resultSetting.idField], list, resultSetting), resultSetting) : defaultOptions.defaultVal) 
          }
        }, 1000)
      } else {
        store.actions.getDictDataObj(type).then((data) => {
          list = data[type]
          if (!list) {
            reject(`${type} `)
            return
          }
          const targetItem = list.find((item) => { return item[resultSetting.idField] === val })
          resolve(targetItem ? defaultOptions.formatFun(GetTreeItemByCode(targetItem[resultSetting.idField], list, resultSetting), resultSetting) : defaultOptions.defaultVal) 
        })
      }
    } else {
      list = dictList[type]
      const targetItem = list.find(item => { return item[resultSetting.idField] === val })
      resolve(targetItem ? defaultOptions.formatFun(GetTreeItemByCode(targetItem[resultSetting.idField], list, resultSetting), resultSetting) : defaultOptions.defaultVal)
    }
  })
}
 
/**
  * @description: codes labels
  * @param {*}
  * @return {*}
  * @author: syx
  */
export function GetTreeLabelByCodesDothings(values, list, defaultVal, formatFunIn, resultSetting) {
  const targetArr = []
  for (let i = 0; i < values.length; i++) {
    const findItem = list.find(item => { return item[resultSetting.idField] === values[i] })
    if (findItem) {
      targetArr.push(formatFunIn(GetTreeItemByCode(findItem[resultSetting.idField], list, resultSetting), resultSetting))
    } else {
      targetArr.push(defaultVal)
    }
  }
  return targetArr
}

export const GetTreeLabelByCodes = (
  vals,
  type,
  options
) => {
  const defaultOptions = {
    defaultVal: "",
    formatFunIn: (arr, treeSetting) => {
      return arr.map((item) => {
        return item[treeSetting.labelField]
      }).join("/")
    },
    treeSetting: {},
    formatFunOut: (arr) => {
      return arr.join(",")
    },
    spacer: ","
  }
  Object.assign(defaultOptions, options)
  return new Promise((resolve, reject) => {
    let values = []
    if (Array.isArray(vals)) {
      values = vals
    } else if (typeof vals === "string") {
      values = vals.split(defaultOptions.spacer)
    }
 
    const {treeSetting} = store.state.dictConfig
    const resultSetting = Object.assign(treeSetting, defaultOptions.treeSetting)
    const dictList = store.state.dictCodes
     
    let list = []
    //
    if (!dictList || !dictList[type]) {
      if (store.state.isGettingDictTypes.includes(type) || store.state.isGettingAllDictTypes) { //  
        const timer = setInterval(() => {
          if (!(store.state.isGettingDictTypes.includes(type) || store.state.isGettingAllDictTypes)) {
            clearInterval(timer)
            list = store.state.dictCodes[type]
            if (!list) {
              reject(`${type} `)
              return
            }
            const targetArr = GetTreeLabelByCodesDothings(values, list, defaultOptions.defaultVal, defaultOptions.formatFunIn, resultSetting)
            resolve(targetArr&&targetArr.length > 0 ? defaultOptions.formatFunOut(targetArr) : defaultOptions.defaultVal)
          }
        }, 1000)
      } else {
        store.actions.getDictDataObj(type).then((data) => {
          list = data[type]
          if (!list) {
            reject(`${type} `)
            return
          }
          const targetArr = GetTreeLabelByCodesDothings(values, list, defaultOptions.defaultVal, defaultOptions.formatFunIn, resultSetting)
          resolve(targetArr&&targetArr.length > 0 ? defaultOptions.formatFunOut(targetArr) : defaultOptions.defaultVal)
        })
      }
    } else {
      list = dictList[type]
      const targetArr = GetTreeLabelByCodesDothings(values, list, defaultOptions.defaultVal, defaultOptions.formatFunIn, resultSetting)
      resolve(targetArr&&targetArr.length > 0 ? defaultOptions.formatFunOut(targetArr) : defaultOptions.defaultVal)
    }
  })
}
 
