<template>
  <van-tree-select ref="VantTreeSelectDict" v-on="$listeners" v-bind="$attrs" :main-active-index.sync="currentMainActiveIndex" :activeId.sync="currentActiveId" :items="items">
    <template
      v-for="(slot, key) in $scopedSlots"
      :slot="key"
      slot-scope="scope"
    >
      <slot :name="key" v-bind="scope"></slot>
    </template>
    <template v-for="(slot, key) in $slots" :slot="key">
      <slot :name="key"></slot>
    </template>
  </van-tree-select>
</template>
<script>
import store from "~/store/index.js"
import {ListToTree} from "~/functions/list-tree.js"
export default {
  name: "van-tree-select-dict",
  data() {
    return {
      list: [], //
      type: "", //
      currentMainActiveIndex: this.mainActiveIndex,
      currentActiveId: this.activeId
    }
  },
  props: {
    mainActiveIndex: {},
    activeId: {},
    //
    maxLevel: {
      type: [Number, String],
      default: "2"
    },
    // 
    treeData: {
      type: Array,
      default: () => {
        return []
      }
    },
    dictType: {
      type: [String, Object]
    }, //
    disableObj: {
      type: Object
    },
    //
    filterDataFun: {
      type: Function
    },
    //
    disabledDataFun: {
      type: Function
    }
  },
  computed: {
    dictCodes() {
      return store.state.dictCodes
    },
    dictConfig() {
      return store.state.dictConfig
    },
    isGettingAllDictTypes() {
      return store.state.isGettingAllDictTypes
    },
    isGettingDictTypes() {
      return store.state.isGettingDictTypes
    },
    endTreeSetting() {
      const props = this.$attrs.fieldNames??{}
      const newProps = {
        idField: props.value??this.dictConfig.treeSetting.idField,
        labelField: props.text??this.dictConfig.treeSetting.labelField,
        childrenField: props.children??this.dictConfig.treeSetting.childrenField
      }
      const treeSetting = Object.assign(this.dictConfig.treeSetting, newProps)
      return treeSetting
    },
    items() {
      if (this.treeData.length > 0) {
        return this.treeData
      }

      const treeSetting = this.dictConfig.treeSetting
      const treeList = this.list.map(item => {
        return {
          id: item[treeSetting.idField],
          text: item[treeSetting.labelField],
          ...item
        }
      })
      const newTreeSetting = {...treeSetting}
      newTreeSetting.idField = "id"
      newTreeSetting.labelField = "text"
      const allAllTreeData = ListToTree(treeList, newTreeSetting)
      const treeArr = JSON.parse(JSON.stringify(allAllTreeData))
      if (this.maxLevel) {
        this.filterLevel(treeArr, +this.maxLevel)
      }
      return treeArr || []
    }
  },
  watch: {
    isGettingDictTypes(val) {
      if (!val.includes(this.type) && this.treeData.length === 0) {
        this.getDictionary_option()
      }
    },
    // 
    isGettingAllDictTypes(val) {
      if (!val && this.treeData.length === 0) {
        this.getDictionary_option() 
      }
    },
    // activeId 
    currentActiveId(val) {
      this.$emit("update: activeId", val)
    },
    // mainActiveIndex 
    currentMainActiveIndex(val) {
      this.$emit("update: mainActiveIndex", val)
    }
  },
  mounted() {
    if (this.treeData.length === 0) {
      this.getDictionary_option()
    }
  },
  methods: {
    getRef() {
      return this.$refs.VantTreeSelectDict
    },
    //
    filterLevel(arr, maxLevel) {
      for (let i = 0; i < arr.length; i++) {
        const item = arr[i]
        const currentLevel = item[this.endTreeSetting.levelField]
        if (currentLevel === maxLevel) {
          delete item[this.endTreeSetting.childrenField]
          continue
        } else {
          item[this.endTreeSetting.childrenField]&&this.filterLevel(item[this.endTreeSetting.childrenField], maxLevel)
        }
      }
    },
    // 
    async getDictionary_option() {
      if (!this.dictType) {
        return
      }
      //
      let dictData = []
      if (typeof this.dictType === "object") {
        this.type = this.dictType.type
        await store.actions.getDictDataObj(this.type)
        dictData = [].concat(this.dictCodes[this.type])
        const { filters, filterType = this.endTreeSetting.idField, reverse = false } = this.dictType
        const filtersArr = typeof (filters) === "string" ? filters.split(",") : filters
        if (reverse) {
          dictData = dictData.filter(item => {
            return !filtersArr.includes(item[filterType])
          })
        } else {
          dictData = dictData.filter(item => {
            return filtersArr.includes(item[filterType])
          })
        }
      } else {
        this.type = this.dictType
        await store.actions.getDictDataObj(this.type)
        dictData = [].concat(this.dictCodes[this.type])
      }

      //
      if (this.filterDataFun) {
        dictData = this.filterDataFun(dictData)
      }
      
      //
      if (this.disabledDataFun) {
        dictData = this.disabledDataFun(dictData)
      }

      if (this.disableObj) {
        const { disableValue, disableType = this.endTreeSetting.idField, reverse = false } = this.disableObj
        const disableList = typeof (disableValue) === "string" ? disableValue.split(",") : disableValue
        if (reverse) {
          dictData = dictData.map(item => {
            const targetItem = JSON.parse(JSON.stringify(item))
            targetItem.disabled = !disableList.includes(targetItem[disableType]) || targetItem.disabled
            return targetItem
          })
        } else {
          dictData = dictData.map(item => {
            const targetItem = JSON.parse(JSON.stringify(item))
            targetItem.disabled = disableList.includes(targetItem[disableType]) || targetItem.disabled
            return targetItem
          })
        }
      }

      this.list = dictData

    }
  }
}
</script>
<style scoped>

</style>