import store from "~/store/index"
import {formatDictData} from "~/utils/index"
/**
 * @description: codelabel
 * @param {*}
 * @return {*}
 * @author: syx
 */
export const GetLabelByCode = (
  val,
  type,
  defaultVal
) => {
  return new Promise((resolve, reject) => {
    const { format } = store.state.dictConfig
    const dictList = store.state.dictCodes
    let list = []
    //
    if (!dictList || !dictList[type]) {
      if (
        store.state.isGettingDictTypes.includes(type) ||
        store.state.isGettingAllDictTypes
      ) {
        //  
        const timer = setInterval(() => {
          if (
            !(
              store.state.isGettingDictTypes.includes(type) ||
              store.state.isGettingAllDictTypes
            )
          ) {
            clearInterval(timer)
            list = store.state.dictCodes[type]
            if (!list) {
              reject(`${type} `)
              return
            }
            const targetItem = list.find((item) => {
              return item[format.value] === val
            })
            resolve(targetItem ? targetItem[format.label] : (defaultVal??""))
          }
        }, 1000)
      } else {
        store.actions.getDictDataObj(type).then((data) => {
          list = data[type]
          if (!list) {
            reject(`${type} `)
            return
          }
          const targetItem = list.find((item) => {
            return item[format.value] === val
          })
          resolve(targetItem ? targetItem[format.label] : (defaultVal??""))
        })
      }
    } else {
      list = dictList[type]
      const targetItem = list.find((item) => {
        return item[format.value] === val
      })
      resolve(targetItem ? targetItem[format.label] : (defaultVal??""))
    }
  })
}

/**
 * @description: codeslabels
 * @param {*}
 * @return {*}
 * @author: syx
 */
export function GetLabelByCodesDothings(
  values,
  list,
  format,
  defaultVal
) {
  const targetArr = []
  for (let i = 0; i < values.length; i++) {
    const findItem = list.find((item) => {
      return item[format.value] === values[i]
    })
    if (findItem) {
      targetArr.push(findItem)
    } else {
      targetArr.push({
        [format.label]: defaultVal,
        [format.value]: values[i]
      })
    }
  }
  return targetArr
}

export const GetLabelByCodes = (
  vals,
  type,
  options
) => {
  const defaultOptions = {
    defaultVal: "",
    formatFun: (arr,
      format) => {
      return arr
        .map((item) => {
          return item[format.label]
        })
        .join(",")
    },
    spacer: ","
  }
  Object.assign(defaultOptions, options) 
  return new Promise((resolve, reject) => {
    let values = []
    if (Array.isArray(vals)) {
      values = vals
    } else if (typeof vals === "string") {
      values = vals.split(defaultOptions.spacer)
    }

    const { format } = store.state.dictConfig
    const dictList = store.state.dictCodes

    let list = []
    //
    if (!dictList || !dictList[type]) {
      if (
        store.state.isGettingDictTypes.includes(type) ||
        store.state.isGettingAllDictTypes
      ) {
        //  
        const timer = setInterval(() => {
          if (
            !(
              store.state.isGettingDictTypes.includes(type) ||
              store.state.isGettingAllDictTypes
            )
          ) {
            clearInterval(timer)
            list = store.state.dictCodes[type]
            if (!list) {
              reject(`${type} `)
              return
            }
            const targetArr = GetLabelByCodesDothings(
              values,
              list,
              format,
              defaultOptions.defaultVal
            )
            resolve(
              targetArr && targetArr.length > 0
                ? defaultOptions.formatFun(targetArr, format)
                : defaultOptions.defaultVal
            )
          }
        }, 1000)
      } else {
        store.actions.getDictDataObj(type).then((data) => {
          list = data[type]
          if (!list) {
            reject(`${type} `)
            return
          }
          const targetArr = GetLabelByCodesDothings(
            values,
            list,
            format,
            defaultOptions.defaultVal
          )
          resolve(
            targetArr && targetArr.length > 0
              ? defaultOptions.formatFun(targetArr, format)
              : defaultOptions.defaultVal
          )
        })
      }
    } else {
      list = dictList[type]
      const targetArr = GetLabelByCodesDothings(
        values,
        list,
        format,
        defaultOptions.defaultVal
      )
      resolve(
        targetArr && targetArr.length > 0
          ? defaultOptions.formatFun(targetArr, format)
          : defaultOptions.defaultVal
      )
    }
  })
}

