/**
 * @description: dictType
 * @param {*}
 * @return {*}
 * @author: syx
 */
export const GetDictObjByDictTypes = (dictTypes) => {
  return new Promise((resolve, reject) => {
    let values = []
    let typeStr = ""
    if (Array.isArray(dictTypes)) {
      typeStr = dictTypes.join(",")
      values = dictTypes
    } else if (typeof dictTypes === "string") {
      values = dictTypes.split(",")
    }

    const dictList = store.state.dictCodes
    const setting = store.state.dictConfig
    //
    if (!dictList) {
      //
      store.mutations.ADD_IS_EGTTING_DICT_TYPES(typeStr)
      setting
        .getDictCodeApi(setting.formatterRequest(setting.query, typeStr))
        .then((data) => {
          //
          store.mutations.REMOVE_IS_EGTTING_DICT_TYPES(typeStr)
          // 
          const dictData = setting.formatterDictList(data, dictTypes)
          // 
          const resultDictData = formatDictData(dictData, setting.filterDataFun, setting.disabledDataFun)

          const copyObj = JSON.parse(JSON.stringify(resultDictData))
          const allDictObj = Object.assign(store.state.dictCodes, copyObj)
          setting.storage.setItem(
            setting.dictDataKey,
            JSON.stringify(allDictObj)
          )
          store.mutations.SET_DICT_CODES(allDictObj)
          resolve(resultDictData)
        })
    } else {
      const needGetArr = [] //
      const haveDictObj = {} //
      for (let i = 0; i < values.length; i++) {
        if (dictList[values[i]]) {
          haveDictObj[values[i]] = dictList[values[i]]
          continue
        }
        needGetArr.push(values[i])
      }

      if (needGetArr.length > 0) {
        //
        store.mutations.ADD_IS_EGTTING_DICT_TYPES(needGetArr.join(","))
        setting
          .getDictCodeApi(
            setting.formatterRequest(setting.query, needGetArr.join(","))
          )
          .then((data) => {
            //
            store.mutations.REMOVE_IS_EGTTING_DICT_TYPES(needGetArr.join(","))
            const dictData = setting.formatterDictList(data, dictTypes)
            // 
            const resultDictData = formatDictData(dictData, setting.filterDataFun, setting.disabledDataFun)
            const copyObj = JSON.parse(JSON.stringify(resultDictData))
            const allDictObj = Object.assign(store.state.dictCodes, copyObj)
            setting.storage.setItem(
              setting.dictDataKey,
              JSON.stringify(allDictObj)
            )
            store.mutations.SET_DICT_CODES(allDictObj)

            const resolveDict = Object.assign(haveDictObj, dictData)

            const noDictTypeArr = []
            for (const key in resolveDict) {
              if (!resolveDict[key] || resolveDict[key].length === 0) {
                noDictTypeArr.push(key)
              }
            }
            if (noDictTypeArr.length > 0) {
              reject(
                `${noDictTypeArr.join(
                  ""
                )} `
              )
            }
            resolve(resultDictData)
          })
      } else {
        resolve(haveDictObj)
      }
    }
  })
}
