<template>
  <van-tag ref="VanTagDict" :type="judgeType" v-on="$listeners" v-bind="$attrs">
    <template
      v-for="(slot, key) in $scopedSlots"
      :slot="key"
      slot-scope="scope"
    >
      <slot :name="key" v-bind="scope"></slot>
    </template>
    <template v-for="(slot, key) in $slots" :slot="key">
      <slot :name="key"></slot>
    </template>
    {{GetLabelByCodeFilter(value, dictType)}}
  </van-tag>
</template>

<script>
import {GetLabelByCodeFilter} from "~/filters/dict"
export default {
  name: "van-tabs-dict",
  props: {
    value: {
      required: true
    },
    dictType: {
      type: [String],
      required: true
    }, //
    judgeTypeFun: {
      type: Function,
      default: () => {

      }
    },
    type: {
      type: String,
      default: "primary"
    }
  },
  data() {
    return {
      GetLabelByCodeFilter
    }
  },
  computed: {
    judgeType() {
      if (!this.judgeTypeFun) {
        return this.type
      }
      return this.judgeTypeFun(this.value) || this.type
    }
  },
  getRef() {
    return this.$refs.VanTagDict
  }
}
</script>

