import store from "~/store/index"
import {
  GetLabelByCodesDothings,
  GetCodeByLabelsDothings
} from "~/functions/dict"
/**
 * @description: codelabel
 * @param {*}
 * @return {*}
 * @author: syx
 */
export const GetLabelByCodeFilter = (val, type, defaultVal) => {
  const { format } = store.state.dictConfig
  const dictList = store.state.dictCodes
  let list = []
  //
  if (!dictList || !dictList[type]) {
    store.actions.getDictDataObj(type).then((data) => {
      list = data[type]
      if (!list) {
        new Error(`${type} `)
        return
      }
      const targetItem = list.find((item) => {
        return item[format.value] === val
      })
      return targetItem ? targetItem[format.label] : defaultVal ?? ""
    })
  } else {
    list = dictList[type]
    const targetItem = list.find((item) => {
      return item[format.value] === val
    })
    return targetItem ? targetItem[format.label] : defaultVal ?? ""
  }
}

export const GetLabelByCodesFilter = (vals, type, options) => {
  const defaultOptions = {
    defaultVal: "",
    formatFun: (arr, format) => {
      return arr
        .map((item) => {
          return item[format.label]
        })
        .join(",")
    },
    spacer: ","
  }
  Object.assign(defaultOptions, options)
  let values = []
  if (Array.isArray(vals)) {
    values = vals
  } else if (typeof vals === "string") {
    values = vals.split(defaultOptions.spacer)
  }

  const { format } = store.state.dictConfig
  const dictList = store.state.dictCodes

  let list = []
  //
  if (!dictList || !dictList[type]) {
    store.actions.getDictDataObj(type).then((data) => {
      list = data[type]
      if (!list) {
        new Error(`${type} `)
        return
      }
      const targetArr = GetLabelByCodesDothings(
        values,
        list,
        format,
        defaultOptions.defaultVal
      )
      return targetArr && targetArr.length > 0
        ? defaultOptions.formatFun(targetArr, format)
        : defaultOptions.defaultVal
    })
  } else {
    list = dictList[type]
    const targetArr = GetLabelByCodesDothings(
      values,
      list,
      format,
      defaultOptions.defaultVal
    )
    return targetArr && targetArr.length > 0
      ? defaultOptions.formatFun(targetArr, format)
      : defaultOptions.defaultVal
  }
}

