<template>
  <div class="van-cascader-dict">
    <van-field v-on="$listeners" v-bind="$attrs" readonly clickable :value="showValue" is-link @click="handleClick" >
      <template v-for="(slot, key) in $scopedSlots" :slot="key" slot-scope="scope">
        <slot :name="key" v-bind="scope"></slot>
      </template>
      <template v-for="(slot, key) in $slots" :slot="key">
        <slot :name="key"></slot>
      </template>
    </van-field>
    <van-popup v-model="showPicker" round position="bottom">
      <van-cascader ref="VantCascaderDict" class="cascader" :placeholder="cascaderPlaceholder" @close="handleClose" @change="handdleChange" @finish="handleFinish" :closeable="false" v-model="selectValue" :field-names="setting" v-bind="$attrs" :options="options">
        <template #title>
          <div class="van-picker__cancel" @click="handleCancel">{{cancelButtonText}}</div>
          <div class="vant_cascader_title">{{$attrs.title}}</div>
          <div class="van-picker__confirm" @click="handleConfirm(selectValue)">{{confirmButtonText}}</div>
        </template>
      </van-cascader>
    </van-popup>
  </div>
</template>
<script>
import store from "~/store/index.js"
import {ListToTree, GetTreePathByValue} from "~/functions/list-tree.js"
export default {
  name: "van-cascader-dict",
  data() {
    return {
      selectValue: null,
      svalue: this.value,
      showValue: "",
      showPicker: false,
      list: [], //
      type: "", //
      selectedOptions: [],
      tabIndex: null //
    }
  },
  props: {
    value: {},
    tips: {
      type: String
    },
    //
    minSelectLevel: {
      type: [Number, String]
    },
    //
    maxLevel: {
      type: [Number, String]
    },
    cascaderPlaceholder: {
      type: String,
      default: ""
    },
    // 
    treeData: {
      type: Array,
      default: () => {
        return []
      }
    },
    defaultValue: {
      type: String,
      default: ""
    },
    spacer: {
      type: String,
      default: "/"
    },
    //
    finish: {
      type: Boolean,
      default: false
    },
    cancelButtonText: {
      type: String,
      default: ""
    },
    confirmButtonText: {
      type: String,
      default: ""
    },
    keyValue: {
      type: Boolean,
      default: false
    },
    dictType: {
      type: [String, Object]
    }, //
    disableObj: {
      type: Object
    },
    //
    filterDataFun: {
      type: Function
    },
    //
    disabledDataFun: {
      type: Function
    }
  },
  computed: {
    dictCodes() {
      return store.state.dictCodes
    },
    dictConfig() {
      return store.state.dictConfig
    },
    isGettingAllDictTypes() {
      return store.state.isGettingAllDictTypes
    },
    isGettingDictTypes() {
      return store.state.isGettingDictTypes
    },
    endTreeSetting() {
      const props = this.$attrs.fieldNames??{}
      const newProps = {
        idField: props.value??this.dictConfig.treeSetting.idField,
        labelField: props.text??this.dictConfig.treeSetting.labelField,
        childrenField: props.children??this.dictConfig.treeSetting.childrenField
      }
      const treeSetting = Object.assign(this.dictConfig.treeSetting, newProps)
      return treeSetting
    },
    options() {
      if (this.treeData.length > 0) {
        return this.treeData
      }
      const allAllTreeData = ListToTree(this.list, this.endTreeSetting)
      const treeArr = JSON.parse(JSON.stringify(allAllTreeData))
      if (this.maxLevel) {
        this.filterLevel(treeArr, +this.maxLevel)
      }
      return treeArr || []
    },
    setting() {
      const props = this.$attrs.fieldNames??{}
      props.value = props.value??this.dictConfig.treeSetting.idField
      props.text = props.label??this.dictConfig.treeSetting.labelField
      props.children = props.children??this.dictConfig.treeSetting.childrenField
      return props
    }
  },
  watch: {
    isGettingDictTypes(val) {
      if (!val.includes(this.type) && this.treeData.length === 0) {
        this.getDictionary_option()
      }
    },
    // 
    isGettingAllDictTypes(val) {
      if (!val && this.treeData.length === 0) {
        this.getDictionary_option() 
      }
    },
    showPicker(val) {
      if (!val) {
        this.selectValue = null
        this.handleClose()
      } else {
        this.selectValue = this.value
      }
    },
    //
    options(val) {
      if (!this.value || val.length === 0) { return }
      const path = GetTreePathByValue(val, this.value, this.endTreeSetting)
      this.showValue = this.setShowValue(path, this.spacer)
    },
    //
    value(val) {
      this.svalue = val//propsresultdatamyResult
    },
    svalue(val, oldVal) {
      if (val !== oldVal) {
        this.$emit("input", val)//myResult
      }
    }
  },
  mounted() {
    if (this.treeData.length === 0) {
      this.getDictionary_option()
    }
     
    if (this.treeData.length > 0 && this.value) {
      const path = GetTreePathByValue(this.treeData, this.value, this.endTreeSetting)
      this.showValue = this.setShowValue(path, this.spacer)
    }
  },
  methods: {
    // 
    setShowValue(targetArr, spacer) {
      return targetArr.map((option) => option[this.endTreeSetting.labelField]).join(spacer)
    },
    //
    filterLevel(arr, maxLevel) {
      for (let i = 0; i < arr.length; i++) {
        const item = arr[i]
        const currentLevel = item[this.endTreeSetting.levelField]
        if (currentLevel === maxLevel) {
          delete item[this.endTreeSetting.childrenField]
          continue
        } else {
          this.filterLevel(item[this.endTreeSetting.childrenField], maxLevel)
        }
      }
    },
    //
    handleFinish(data) {
      if (this.finish) {
        this.handleConfirm(data.value)
      }
      this.$emit("finish", data)
    },
    //
    handdleChange(data) {
      this.tabIndex = data.tabIndex
      this.selectedOptions = data.selectedOptions 
      this.$emit("change", data)
    },
    //
    handleClose() {
      this.$emit("close")
    },
    //
    handleClick() {
      if (this.$attrs.disabled === true || this.$attrs.disabled === "") {
        return
      }
      this.showPicker = true
    },
    //
    handleConfirm(value) {
      if (this.selectedOptions[this.tabIndex].disabled) {
        return 
      }
      if (this.minSelectLevel&& +this.minSelectLevel > (this.tabIndex + 1)) {
        const msg = this.tips || `${this.minSelectLevel}`
        this.$toast(msg)
        return
      }
      this.svalue = value
      this.showPicker = false
      if (!this.keyValue) {
        this.$emit("dictChange", this.svalue)
      } else {
        this.$emit("dictChange", this.selectedOptions)
      }
      this.showValue = this.setShowValue(this.selectedOptions, this.spacer)
    },
    //
    handleCancel() {
      this.showPicker = false
    },
    // 
    async getDictionary_option() {
      if (!this.dictType) {
        return
      }
      //
      let dictData = []
      if (typeof this.dictType === "object") {
        this.type = this.dictType.type
        await store.actions.getDictDataObj(this.type)
        dictData = [].concat(this.dictCodes[this.type])
        const { filters, filterType = this.endTreeSetting.idField, reverse = false } = this.dictType
        const filtersArr = typeof (filters) === "string" ? filters.split(",") : filters
        if (reverse) {
          dictData = dictData.filter(item => {
            return !filtersArr.includes(item[filterType])
          })
        } else {
          dictData = dictData.filter(item => {
            return filtersArr.includes(item[filterType])
          })
        }
      } else {
        this.type = this.dictType
        await store.actions.getDictDataObj(this.type)
        dictData = [].concat(this.dictCodes[this.type])
      }

      //
      if (this.filterDataFun) {
        dictData = this.filterDataFun(dictData)
      }

      //
      if (this.disabledDataFun) {
        dictData = this.disabledDataFun(dictData)
      }

      if (this.disableObj) {
        const { disableValue, disableType = this.endTreeSetting.idField, reverse = false } = this.disableObj
        const disableList = typeof (disableValue) === "string" ? disableValue.split(",") : disableValue
        if (reverse) {
          dictData = dictData.map(item => {
            const targetItem = JSON.parse(JSON.stringify(item))
            targetItem.disabled = !disableList.includes(targetItem[disableType]) || targetItem.disabled
            return targetItem
          })
        } else {
          dictData = dictData.map(item => {
            const targetItem = JSON.parse(JSON.stringify(item))
            targetItem.disabled = disableList.includes(targetItem[disableType]) || targetItem.disabled
            return targetItem
          })
        }
      }
      this.list = dictData
    },

    getRef() {
      return this.$refs.VantCascaderDict
    }
  }
}
</script>
<style scoped>
.van-cascader-dict >>> .van-popup .van-cascader .van-cascader__header {
  padding: 0;
}
.van-cascader-dict >>> .van-popup .van-cascader .van-cascader__header .van-cascader__title {
  width: 100%;
  display: flex;
  justify-content: flex-start;
  align-content: center;
}
.van-cascader-dict >>> .van-popup .van-cascader .van-cascader__header .van-cascader__title .van-picker__cancel {
  color: #969799;
}
.van-cascader-dict >>> .van-popup .van-cascader .van-cascader__header .van-cascader__title .vant_cascader_title {
  flex-grow: 1;
  text-align: center;
  font-weight: 500;
  font-size: 16px;
  line-height: 20px;
}
.van-cascader-dict >>> .van-popup .van-cascader .van-cascader__header .van-cascader__title .van-picker__confirm {
  color: #576b95;
}

</style>