//
async function getDIctCodeReq(setting) {
  // 
  const dictData = setting.storage.getItem(setting.dictDataKey)
  //usuallyGetFileds       
  const values = setting.usuallyGetFileds.split(",")
  const needGetArr = [] //
  if (values.length > 0) {
    for (let i = 0; i < values.length; i++) {
      if (dictData[values[i]]) {
        continue
      }
      needGetArr.push(values[i])
    }
  }
 
  if (!dictData || setting.getDictEveryTime || needGetArr.length > 0) {
    let data
    let query
    if (setting.isGetAll) {
      query = ""
      store.mutations.SETTING_ISGETTING_ALL_DICT_TYPES(true) // true   
      data = await setting.getDictCodeApi(setting.formatterRequest(setting.query))
      store.mutations.SETTING_ISGETTING_ALL_DICT_TYPES(false) //  false   
    } else if (needGetArr.length > 0) {
      query = needGetArr.join(",")
      store.mutations.ADD_IS_EGTTING_DICT_TYPES(needGetArr.join(",")) //
      data = await setting.getDictCodeApi(setting.formatterRequest(setting.query, query))
      store.mutations.REMOVE_IS_EGTTING_DICT_TYPES(needGetArr.join(",")) //
    }
    //
    const dictCodeList = setting.formatterDictList(data, query)
    const version = setting.formatterDictversion(data)
    setting.storage.setItem(setting.versionKey, version) //

    //
    const dictObjs = Object.assign(JSON.parse(JSON.stringify(setting.localDictCodes)), dictCodeList)
    store.mutations.SET_DICT_CODES(dictObjs)
    setting.storage.setItem(setting.dictDataKey, JSON.stringify(dictObjs)) //
    
  }
}

const vue2ElementDict = {
  async install(Vue, config) {
    console.dir(Vue)
    /**
     * @description: 
     * @param {*} Vue
     * @param {*} config :{
     *    axiosSetting,    axios   requestName
     *    getDictCodeApi,    
     *    version,     
     *    query,    type
     *    usuallyGetFileds,      
     *    versionKey, 
     *    dictDataKey, 
     *    format, 
     *    formatterDictList,  
     *    formatterDictversion, 
     *    formatterRequest, 
     *    storage,    localstorage   sessionstorage
     *    isGetAll,  
     *    treeSetting, 
     *    localDictCodes, 
     *    getDictEveryTime  
     *    filterDataFun  
     *    disabledDataFun  
     * }
     * @return {*}
     * @author: syx
     */
    const defaultConfig = {
      // 
      filterDataFun: (list) => {
        return list
      },
      // 
      disabledDataFun: (list) => {
        return list
      },
      //
      treeSetting: {
        idField: "id", //
        parentIdField: "parentId", //
        childrenField: "children", //
        firstId: "0", // 
        labelField: "label", //label
        labelArrField: "labelArr", //
        idArrField: "idArr", //id
        levelField: "level", //
        level: 0, // 
        leafField: "leaf" //
      },
      query: "dictType",
      versionKey: "vue2VantDictVersion",
      dictDataKey: "vue2VantDictData",
      format: {
        value: "value",
        label: "label"
      },
      formatterRequest: (query, dictType) => {
        if (!dictType) {
          return { [query]: "" }
        }
        return { [query]: dictType }
      },
      formatterDictList: (
        data,
        query
      ) => {
        return data?.dictData
      },
      formatterDictVersion: (data) => {
        return data.version
      },
      storage: localStorage,
      usuallyGetFileds: "",
      isGetAll: false,
      localDictCodes: {}, //
      getDictEveryTime: false
    }
    config.treeSetting = Object.assign(defaultConfig.treeSetting, config.treeSetting)
    const setting = Object.assign(defaultConfig, config)

    store.mutations.SET_DICT_SETTING(setting)
    const dictList = JSON.parse(setting.storage.getItem(setting.dictDataKey)) || {}
    //
    let dictObjs = JSON.parse(JSON.stringify(setting.localDictCodes))
    dictObjs = Object.assign(dictList, dictObjs)
    setting.storage.setItem(setting.dictDataKey, JSON.stringify(dictObjs))
    store.mutations.SET_DICT_CODES(dictObjs)

    //
    const currentversion = setting.storage.getItem(setting.versionKey) //
    if (setting.version !== currentversion) {
      setting.storage.removeItem(setting.dictDataKey) //
      setting.storage.setItem(setting.versionKey, setting.version) //

      setting.storage.setItem(setting.dictDataKey, JSON.stringify(setting.localDictCodes))
      // 
      store.mutations.SET_DICT_CODES(setting.localDictCodes)
    }
    if (setting.isGetAll || setting.usuallyGetFileds) {
    //   
      getDIctCodeReq(setting)
    }

    // 
    components.map(component => Vue.component(component.name, component))

    // 
    filters.map(filter => {
      Vue.filter(filter.name, filter.func)
    })
  }
}

export default vue2ElementDict